<template>
  <div>
    <el-input placeholder="Busque um campo" v-model="filter.search" clearable></el-input>
    <el-divider></el-divider>
    <ul id="listFields" :class="objeto">
      <li v-for="field in filteredItems" :key="field.id" @click="addField(field)">
        <i class="fas fa-plus-circle"></i>
        {{ field.label }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "RelatoriosListField",
  props: ["objeto"],
  data() {
    return {
      filter: {
        search: ""
      },
      fields: [
        {
          id: 1,
          label: "Primeiro Nome",
          type: "text"
        },
        {
          id: 2,
          label: "Sobrenome",
          type: "text"
        },
        {
          id: 3,
          label: "E-mail",
          type: "text"
        }
      ]
    };
  },
  methods: {
    addField(field) {
      console.log(field.id);
    }
  },
  computed: {
    filteredItems() {
      if (this.filter.search != "") {
        return this.fields.filter(field => {
          return (
            field.label
              .toLowerCase()
              .indexOf(this.filter.search.toLowerCase()) > -1
          );
        });
      } else {
        return this.fields;
      }
    }
  }
};
</script>

